import React, { useEffect, useState } from "react"
import styled, { keyframes } from "styled-components"
import Indicator from "../atoms/Indicator"
import WebsiteContent from "./WebsiteContent"
import { ArrowRightS } from "@styled-icons/remix-line/ArrowRightS"

const moveArrow = keyframes`
  0%, 100%{
      transform:translateX(0);
  }
  50%{
      transform:translateX(10px);
  }
`

const StyledContainer = styled.div`
  display: flex;
  position: relative;
  margin: 0 auto 70px auto;
  padding-bottom: 70px;
  cursor: default;
  overflow: hidden;
  @media (max-width: 1000px) {
    flex-direction: column;
    padding: 0;
  }
  @media (min-width: 1400px) {
    width: 70%;
  }
`

const TitleImageContainer = styled.div`
  position: relative;
  width: 80%;
  display: flex;
  flex-direction: column;
  text-align: left;
  @media (min-width: 1001px) {
    margin: 0 30px 50px 30px;
  }
  @media (max-width: 1000px) {
    width: 100%;
    text-align: center !important;
  }
`

const StyledTitle = styled.h1`
  position: relative;
  display: inline-block;
  margin: 20px 0;
  font-size: 4vw;
  font-weight: 300;
  text-transform: uppercase;
  color: #000;
  @media (max-width: 1000px) {
    font-size: 9vw;
  }
`

const ImageContainer = styled.div`
  position: relative;
  width: 100%;
  :hover {
    > div {
      opacity: 0.6;
    }
    .desktopView {
      transform: translate(-50%, -50%) scale(1.1);
    }
  }
`

const WebsiteImage = styled.img`
  position: relative;
  width: 100%;
  height: 100%;
  box-shadow: 0 25px 35px rgba(0, 0, 0, 0.3);
  @media (max-width: 1000px) {
    box-shadow: none;
  }
`

const Layer = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(0, 0, 0);
  opacity: 0.3;
  transition: opacity 1s ease;
  &.lighterBackground {
    opacity: 0.1;
  }
`

const WebsiteLink = styled.a`
  position: absolute;
  right: 10px;
  top: 10px;
  transition: transform 1s;
  text-decoration: none;
  cursor: pointer;
  color: #fff;
  font-size: 35px;
  &.desktopView {
    right: unset;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    > svg {
      margin-bottom: 4px;
      animation: ${moveArrow} 1s ease-in-out infinite;
    }
  }
`

const WebsiteCard = ({
  src,
  color,
  alt,
  link,
  title,
  description,
  technologies,
  lighterBackground,
}) => {
  const [mobileView, setmobileView] = useState(false)

  useEffect(() => {
    const HideAndShowContent = () => {
      const containers = document.getElementsByClassName("websiteContainer")
      Array.from(containers).forEach(container => {
        document.fonts.ready
          .then(() => {
            const containerContent = container.getElementsByClassName(
              "websiteContent"
            )[0]
            const containerContentHeight = containerContent.offsetHeight

            containerContent.style.marginTop = containerContent.classList.contains(
              "inactive"
            )
              ? "-" + containerContentHeight + "px"
              : "0px"
          })
          .then(() => {
            container.getElementsByClassName(
              "websiteContent"
            )[0].style.transition = "all 0.5s ease-in-out"
          })
      })
    }

    const MoveContentUpDown = () => {
      const parallaxContents = document.getElementsByClassName(
        "vertical-parallax"
      )
      Array.from(parallaxContents).forEach(content => {
        var viewportOffset = content.getBoundingClientRect()
        const bottomOffset = 0
        const topOffset = 0
        let percentageHeightPositionOnScreen =
          (viewportOffset.top -
            window.innerHeight * topOffset +
            content.offsetHeight * 0.5) /
          (window.innerHeight * (1 - topOffset - bottomOffset))

        percentageHeightPositionOnScreen = Math.max(
          0,
          Math.min(1, percentageHeightPositionOnScreen)
        )
        let transform = `translateY(${percentageHeightPositionOnScreen * 100}%)`
        content.style.transform = transform
      })
    }

    document.addEventListener("scroll", () => {
      if (!mobileView) MoveContentUpDown()
    })

    window.addEventListener("resize", () => {
      setmobileView(window.innerWidth <= 1000)
    })

    if (mobileView) {
      HideAndShowContent()
    }
  })
  const [activeContent, setActiveContent] = useState(false)
  const contentToggle = () => {
    setActiveContent(x => !x)
  }

  return (
    <StyledContainer className="websiteContainer">
      <TitleImageContainer className="imgTitleContainer">
        <StyledTitle>{title}</StyledTitle>
        {mobileView ? (
          <WebsiteImage alt={alt} src={src} onClick={contentToggle} />
        ) : (
          <ImageContainer>
            <WebsiteImage alt={alt} src={src} onClick={contentToggle} />
            <Layer className={lighterBackground} />
            <WebsiteLink className="desktopView" href={link} target="_blank">
              Go to
              <ArrowRightS size="40" />
            </WebsiteLink>
          </ImageContainer>
        )}
      </TitleImageContainer>
      <Indicator
        active={activeContent ? "active" : ""}
        onClick={contentToggle}
      />
      <WebsiteContent
        active={activeContent ? "active" : "inactive"}
        title={title}
        link={link}
        description={description}
        technologies={technologies}
        color={color}
      />
    </StyledContainer>
  )
}

export default WebsiteCard
