import React, { useEffect, useState } from "react"
import styled, { keyframes } from "styled-components"
import photo from "../../images/photo.jpg"
import TechnologyIcons from "../molecules/TechnologyIcons"
import { isHigherThanHalfOfTheScreen } from "../../helpers/functions"

const fadeInRight = keyframes`
	0%
	{
		transform: translateX(10vw);
	}
  100%
	{
		opacity: 1;
		transform: translateX(0);

	}
`

const fadeInLeft = keyframes`
	0%
	{
		transform: translateX(-10vw);
	}
	100%
	{
		transform: translateX(0);
		opacity: 1;
  }
`

const fadeInBottom = keyframes`
{
	0%
	{
		transform: translateY(10vh);
	}
	100%
	{
		transform: translateY(0);
		opacity: 1;
	}
}`

const StyledWrapper = styled.div`
  cursor: default;
  overflow: hidden;
  display: grid;
  padding: 50px 200px;
  align-items: center;
  @media (max-width: 1270px) {
    padding: 50px 100px;
  }

  @media (max-width: 800px) {
    padding: 30px 5px 0 5px;
  }
`

const StyledImage = styled.img`
  width: 450px;
  height: 600px;
  opacity: 0;
  box-shadow: 25px 25px 35px rgba(0, 0, 0, 0.3);
  margin: 0 auto;
  grid-row: 1/3;
  grid-column: 2/3;
  animation: ${fadeInRight} 1s ease forwards;
  animation-play-state: paused;
  animation-delay: 1s;
  @media (max-width: 1600px) {
    width: 374px;
    height: 500px;
  }
  @media (max-width: 1300px) {
    width: 300px;
    height: 400px;
    grid-row: 2/3;
    grid-column: 1/1;
  }
  @media (max-width: 320px) {
    width: 150px;
    height: 200px;
  }
  @media (max-width: 200px) {
    width: 75px;
    height: 100px;
  }
`

const StyledDescription = styled.div`
  opacity: 0;
  animation: ${fadeInLeft} 1s ease forwards;
  animation-play-state: paused;
  margin: 0 auto;
  max-width: 700px;
  text-align: center;
  font-weight: 400;
  font-size: 1.8vw;
  color: #000;
  grid-row: 1/2;
  font-family: "Raleway";
  grid-column: 1/2;
  @media (max-width: 1300px) {
    line-height: 35px;
    margin: 25px auto;
    font-size: 3vw;
    padding: 0 20px;
    grid-column: 1/1;
    grid-row: 1/2;
  }
  @media (max-width: 700px) {
    font-size: 5.5vw;
  }
`

const StyledTechnologies = styled.div`
  opacity: 0;
  animation: ${fadeInBottom} 1s ease forwards;
  animation-play-state: paused;
  grid-row: 2/3;
  grid-column: 1/2;
  @media (max-width: 1300px) {
    grid-column: 1/1;
    grid-row: 3/4;
  }
`

const TechnologiesHeader = styled.h1`
  text-align: center;
  font-size: 2vw;
  text-transform: uppercase;
  margin-bottom: 60px;
  font-weight: 400;
  @media (max-width: 1300px) {
    font-size: 6vw;
    margin: 50px 0 30px 0;
  }
`

const TechnologyName = styled.div`
  margin: auto;
  min-height: 60px;
  margin-top: 40px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  text-transform: uppercase;
  color: ${theme => theme.color};
  font-size: 30px;
  @media (max-width: 1000px) {
    position: static;
    transform: none;
  }
`

const AboutMeSection = () => {
  const [technologyTitle, setTechnologyTitle] = useState("")
  const [technologyColor, setTechnologyColor] = useState("")

  useEffect(() => {
    const setStylesInDescription = () => {
      const aboutMeContent = document.getElementById("aboutMeContent")
      const description = document.getElementById("description")
      const technologies = document.getElementById("technologies")
      const rightSide = document.getElementById("rightSide")
      if (isHigherThanHalfOfTheScreen(aboutMeContent)) {
        rightSide.style.animationPlayState = "running"
        description.style.animationPlayState = "running"
        technologies.style.animationPlayState = "running"
      }
    }
    setStylesInDescription()
    document.addEventListener("scroll", function (ev) {
      setStylesInDescription()
    })
  })

  return (
    <StyledWrapper id="aboutMeContent">
      <StyledDescription id="description">
        Hi! My name is Emilia. I'm 22 years old. Currently I live in Gdańsk. I'm
        looking for my first full-time job as a Junior Frontend Developer.
      </StyledDescription>
      <StyledTechnologies id="technologies">
        <TechnologiesHeader>Technologies I use</TechnologiesHeader>
        <TechnologyIcons
          setTitle={setTechnologyTitle}
          setColor={setTechnologyColor}
        />
        <TechnologyName color={technologyColor}>
          {technologyTitle}
        </TechnologyName>
      </StyledTechnologies>
      <StyledImage id="rightSide" src={photo} alt="Emilia Flisikowska" />
    </StyledWrapper>
  )
}
export default AboutMeSection
