import React, { useEffect } from "react"
import styled, { keyframes } from "styled-components"
import { Linkedin } from "@styled-icons/feather/Linkedin"
import { Facebook } from "@styled-icons/feather/Facebook"
import { Github } from "@styled-icons/feather/Github"
import { isInViewport } from "../../helpers/functions"

const fadeInBottom = keyframes`
{
	0%
	{
		transform: translateY(30px);
	}
	100%
	{
		transform: translateY(0);
		opacity: 1;
	}
}
`

const StyledContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  animation: ${fadeInBottom} 0.4s linear forwards;
  animation-play-state: paused;
  opacity: 0;
  &:nth-child(1) {
    animation-delay: 0s;
  }
  &:nth-child(2) {
    animation-delay: 0.25s;
  }
  &:nth-child(3) {
    animation-delay: 0.5s;
  }
`

const IconWrapper = styled.a`
  border: 2px solid #fda403;
  border-radius: 100%;
  margin: 10px;
  transition: 0.5s ease;
  @media (max-width: 280px) {
    margin: 5px;
  }
  svg {
    transition: 0.5s ease;
    color: #fda403;
    width: 30px;
    height: 30px;
    margin: 15px;
    @media (max-width: 280px) {
      height: 20px;
      width: 20px;
      margin: 10px;
    }
  }
  :hover {
    background-color: #fda403;
    svg {
      color: #eee;
    }
  }
`

const MediaIcons = () => {
  useEffect(() => {
    document.addEventListener("scroll", function (ev) {
      const MediaIcons = document.getElementById("mediaIcons")

      if (isInViewport(MediaIcons))
        MediaIcons.style.animationPlayState = "running"
    })
  })
  return (
    <StyledContainer id="mediaIcons">
      <IconWrapper
        href="https://www.linkedin.com/in/flisikowska"
        target="_blank"
      >
        <Linkedin alt="Linkedin" />
      </IconWrapper>
      <IconWrapper
        href="https://www.facebook.com/emilia.flisikowska.1"
        target="_blank"
      >
        <Facebook alt="Facebook" />
      </IconWrapper>
      <IconWrapper href="https://github.com/flisikowska" target="_blank">
        <Github alt="Github" />
      </IconWrapper>
    </StyledContainer>
  )
}
export default MediaIcons
