import React from "react"
import styled from "styled-components"
// import MyButton from "../atoms/Button"
import { Link } from "@styled-icons/octicons/Link"

const StyledContainer = styled.div`
  overflow: none;
  z-index: 10;
  @media (max-width: 1000px) {
    overflow: hidden;
    position: relative;
    width: 100%;
  }
`
const StyledContent = styled.div`
  position: absolute;
  right: 0;
  padding: 40px;
  background: #4a4a4a;
  max-width: 400px;
  text-align: right;
  color: #fff;
  @media (max-width: 1000px) {
    position: unset;
    width: 100%;
    max-width: none;
    text-align: center !important;
  }
  @media (min-width: 1001px) {
    border: 3px solid ${props => props.color};
    :before {
      content: "";
      position: absolute;
      top: 50px;
      left: -3px;
      z-index: -5;
      width: calc(100% + 6px);
      height: calc(100% - 100px);
      background: #4a4a4a;
      transition: 0.5s ease-in-out;
      transform: scaleY(1);
      transition-delay: 0.3s;
    }
    :hover:before {
      transform: scaleY(0);
    }
    :after {
      z-index: -5;
      content: "";
      position: absolute;
      left: 50px;
      top: -3px;
      height: calc(100% + 6px);
      width: calc(100% - 100px);
      background: #4a4a4a;
      transition: 0.5s ease-in-out;
      transform: scaleX(1);
    }

    :hover:after {
      transform: scaleX(0);
    }
    :hover .button > p {
      /* :before {
        transform-origin: right;
        transition: transform 0.5s ease;
        transform: scaleX(0);
      } */
    }
  }
`
const StyledTitle = styled.h2`
  font-size: 2vw;
  font-weight: 500;
  margin: 0;
  text-align: right;
  position: relative;
  color: ${props => props.color};
  @media (max-width: 1300px) {
    font-size: 3vw;
  }
  @media (max-width: 1000px) {
    font-size: 7vw;
    text-align: center !important;
  }
`

const StyledLink = styled.a`
  font-weight: 400;
  margin: 0;
  font-size: 1vw;
  color: #aaa;
  text-decoration: none;
  transition: all 0.5s;
  :hover {
    color: #eee;
  }
  @media (max-width: 1300px) {
    font-size: 1.5vw;
  }
  @media (max-width: 1000px) {
    font-size: 3.5vw;
  }
`

const StyledDescription = styled.p`
  font-size: 0.8em;
  font-weight: 300;
  position: relative;
  text-align: right;
  @media (max-width: 1000px) {
    text-align: justify !important;
    text-align-last: center;
  }
  @media (max-width: 500px) {
    font-size: 0.7em;
  }
  > span {
    font-size: 0.7em;
    display: block;
    margin-top: 20px;
  }
`
const WebsiteLinkIcon = styled.a`
  position: absolute;
  top: 20px;
  right: 20px;
  text-decoration: none;
  color: #fff;
  @media (min-width: 1000px) {
    display: none;
  }
  > svg {
    transition: 0.6s ease;
    transition-delay: 0.8s;
  }
  &.active {
    > svg {
      transform: translateX(0);
      opacity: 1;
    }
  }
  &.inactive {
    > svg {
      transform: translateX(10vw);
      opacity: 0;
    }
  }
`

const WebsiteContent = ({
  color,
  active,
  title,
  link,
  description,
  technologies,
}) => {
  return (
    <StyledContainer>
      <StyledContent
        color={color}
        className={`${active} websiteContent vertical-parallax`}
      >
        <StyledTitle className="contentTitle" color={color}>
          {title}
        </StyledTitle>
        <StyledLink className="link" href={link} target="_blank">
          {link}
        </StyledLink>
        <StyledDescription className="contentDescription">
          {description}
          <span>{technologies}</span>
        </StyledDescription>
        <WebsiteLinkIcon className={active} href={link} target="_blank">
          <Link size="40" />
        </WebsiteLinkIcon>
        {/* <MyButton href={link} color={color} text="GO TO" /> */}
      </StyledContent>
    </StyledContainer>
  )
}
export default WebsiteContent
