import React from "react"
import styled, { keyframes } from "styled-components"
import { ArrowIosDownwardOutline } from "@styled-icons/evaicons-outline/ArrowIosDownwardOutline"

const animateArrow = keyframes`
{
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
`
const StyledContainer = styled.div`
  display: none;
  @media (max-width: 1000px) {
    display: inline-block;
    position: absolute;
    width: 100%;
    bottom: 0;
    z-index: 100;
    transition: transform 0.5s;
    text-align: center;
    &.active {
      transform: rotate(180deg);
    }
  }
`

const StyledArrow = styled(ArrowIosDownwardOutline)`
  transition: transform 0.5s;
  animation: ${animateArrow} 1s linear infinite;

  &.active {
    animation: none;
  }
`

const Indicator = ({ active, onClick }) => (
  <StyledContainer className={active} onClick={onClick}>
    <StyledArrow size="40" className={`${active} indicator`} />
  </StyledContainer>
)

export default Indicator
